import React, { useState } from "react";
import { Grid, Box, Heading, Layer, Button, Spinner } from "grommet";
import { Outlet } from "react-router";
import AppHeader from "./AppHeader";
import AppSidebar from "./AppSidebar";
import globalConfig from "../globalConfig";
import { Alert, Validate } from "grommet-icons";
/*
a layout component that organizes the application UI into a grid structure using Grommet's Grid component. 
It includes a company logo area, header, sidebar, and main content area. 
The layout sets up the structure for rendering other components and provides a consistent visual structure for the application.
*/
const Layout = () => {
  const DEFAULT_PROGRESS = {
    message: "",
    state: "progress",
    closeable: true,
  };
  const [alertMessage, setAlertMessage] = useState("");
  const [progress, setProgress] = useState(DEFAULT_PROGRESS);

  const showAlert = (message) => setAlertMessage(message);
  const showProgress = (message, state, closeable = true) =>
    setProgress({ message, state, closeable });

  globalConfig.showAlert = showAlert;
  globalConfig.showProgress = showProgress;

  return (
    <Grid
      fill
      rows={["xsmall", "flex"]}
      columns={["small", "flex"]}
      //[column,row] coordinates for start and end of areas
      areas={[
        { name: "companyLogo", start: [0, 0], end: [1, 0] },
        { name: "header", start: [1, 0], end: [1, 0] },
        { name: "sidebar", start: [0, 1], end: [0, 1] },
        { name: "main", start: [1, 1], end: [1, 1] },
      ]}
    >
      <Box
        gridArea="companyLogo"
        justify="center"
        align="center"
        background="primary-2"
      >
        <Heading flex pad="none" level={3}>
          {"TiDe"}
        </Heading>
      </Box>
      <Box gridArea="header" background="lightPrimary">
        <AppHeader />
      </Box>
      <Box gridArea="sidebar" background="darkPrimary">
        <AppSidebar />
      </Box>
      <Box gridArea="main" background="lightTint" overflow="auto">
        <Outlet />
      </Box>
      {!!alertMessage && (
        <Layer
          onEsc={() => setAlertMessage("")}
          onClickOutside={() => setAlertMessage("")}
        >
          <Box pad="medium" justify="center" align="center">
            <Heading level={5}>{alertMessage}</Heading>
            <Box direction="row">
              <Button label="Close" onClick={() => setAlertMessage("")} />
            </Box>
          </Box>
        </Layer>
      )}
      {!!progress.message && (
        <Layer
          onEsc={() => progress.closeable && setProgress(DEFAULT_PROGRESS)}
          onClickOutside={() =>
            progress.closeable && setProgress(DEFAULT_PROGRESS)
          }
        >
          <Box pad="medium" justify="center" align="center">
            {progress.state === "progress" && <Spinner />}
            {progress.state === "error" && <Alert color="red" />}
            {progress.state === "complete" && <Validate color="green" />}
            <Heading level={5}>{progress.message}</Heading>
            {progress.state === "error" ||
              (progress.state === "complete" && (
                <Box direction="row">
                  <Button
                    label="Close"
                    onClick={() => setProgress(DEFAULT_PROGRESS)}
                  />
                </Box>
              ))}
          </Box>
        </Layer>
      )}
    </Grid>
  );
};

export default Layout;
